@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;600;700;800&display=swap');

*, *::before, *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Poppins', sans-serif;
    position: relative;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
}


a {
    text-decoration: none;
    color: inherit;
}

.active-link {
    font-weight: bold;
    color: #ffbad3;
}

nav {
    background-color: #f9f9f9;
    box-shadow: 0 2px 4px #c1c1c1;
    display: flex;
    align-items: center;
    padding: 1.5rem 2rem;
    gap: 1rem;
    color: #fad9e5;
    font-weight: 500;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
}

nav a:hover {
    color: #FDCEDF;
}

nav a:hover img {
    filter: invert(78%) sepia(26%) saturate(416%) hue-rotate(299deg) brightness(107%) contrast(102%);
}

nav h2 {
    margin-right: auto;
    font-weight: 800;
    color: #ffbad3;
}

nav h2 a {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

header {
    color: #ffbad3;
    text-align: center;
    padding: 3rem 0;
    font-size: 2.5rem;
    font-weight: 800;
}

section {
    padding: 6rem 2rem 0;
}

.cats {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
}

.cat {
    align-self: flex-start;
    width: 200px;
    height: 300px;
    flex: 0 1 auto;
    display: flex;
    flex-direction: column;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 2px 2px 4px #c1c1c1;
    cursor: pointer;
}

.cat-thumbnail {    
    object-fit: cover;  
    object-position: center;
    flex-grow: 1;
}

.cat-title {
    padding: 0.5rem 0.5rem 1.5rem;
    color: #242424;
}

.cat-origin {
    font-size: 0.8rem;
    color: #464646;
}

.cat-length {
    font-size: 0.6rem;
    color: #787878;
    margin-top: 0.3rem;
}

.back {
    color: #ffbad3;
}

.back:hover {
    color: #FDCEDF;
    text-decoration: underline;
}

.cat-info {
    display: flex;
    justify-content: center;
    padding: 1rem;
}

.cat-card {
    box-shadow: 2px 2px 4px #c1c1c1;
    width: 100%;
    max-width: 800px;
    border-radius: 10px;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    height: max-content;
}

.cat-card > * {
    flex: 1 1 auto;
}

.cat-card .image {
    min-height: 60vh;
    min-width: 45%;
}

.cat-card .details {
    padding: 2rem;
    /* min-height: 90%; */
}

.cat-card .details h1 {
    font-size: 2rem;
    color: #242424;
    line-height: 1;
}

.cat-card .details h3 {
    font-size: 1rem;
    color: #787878;
}

.cat-card .details .stats {
    margin-top: 1rem;
    font-size: 0.8rem;
    color: #242424;
}

footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    padding: 7rem;
    font-size: 0.8rem;
    color: #ffbad3;
    margin-top: auto;
}

footer img, nav img {
    filter: invert(91%) sepia(17%) saturate(2947%) hue-rotate(290deg) brightness(119%) contrast(101%);}